<template>
	<div id="navbar">
		<nav class="nav-extended z-depth-0">
			<div class="nav-wrapper">
				<a href="#" class="brand-logo center" style="color: #897E73">Fabrice Bulteau</a>
			</div>
			<ul class="tabs">
				<li class="tab" id="home"><a  href="#" style="color: #897E73;" @click="openMenu()">Home</a></li>
				<li class="tab"><a  href="#" style="color: #897E73;" @click="openPhotos()">Photos</a></li>
				<li class="tab"><a  href="#" style="color: #897E73;" @click="openContact()">Contact</a></li>
			</ul>
  		</nav>
		<br><br>
	</div>
</template>

<script>
	export default {
		name: 'NavBar',

		data () {
			return {
			}
		},

		methods: {
			openMenu: function () {
				this.$router.push({name: 'Menu'});
			},
			openContact: function () {
				this.$router.push({name: 'Contact'});
			},
			openPhotos: function () {
				this.$router.push({name: 'Photos'});
			}
		}
	}
</script>

<style scoped>
	nav {
		background-color: white;
		color: #897E73;
		
		border-top-style: solid;
		border-top-color: #897E73;
		border-top-width: 1px;

		border-bottom-style: solid;
		border-bottom-color: #897E73;
		border-bottom-width: 1px;
	}

	.nav-wrapper {
		/*border-bottom-style: solid;
		border-bottom-color: #897E73;
		border-bottom-width: 1px;*/
	}

	a {
		color: #897E73;
	}
</style>


